dding-top: 2px;\n    font-size: 0.75em;\n    color: rgb(255, 255, 255);\n    white-space: nowrap;\n}\n\n.divInfo {\n    z-index: 2147483647;\n    width: 100%;\n    position: absolute;\n    border-radius: 5px;\n    bottom: 85%;\n    height: 1px;\n    width: 80%;\n    display: block;\n    position: absolute;\n    left: 10%;\n}\n\n.divBigPlayBtn {\n    z-index: 2147483647;\n    width: 100%;\n    position: absolute;\n    border-radius: 5px;\n    bottom: 60%;\n    height: 1px;\n    width: 80%;\n    display: block;\n    position: absolute;\n    left: 45%;\n}\n\n.divTspanitle {\n    position: relative;\n    visibility: visible;\n    background-color: rgba(0, 0, 0, 0.5);\n    color: rgb(255, 255, 255);\n    text-overflow: ellipsis;\n    overflow: hidden;\n    margin: 0px;\n    white-space: nowrap;\n    display: inline-block;\n    max-width: 766px;\n    font-size: 12px;\n    padding: 10px;\n    /* Add this option to set circular motifs */\n    /*border-radius:100px;*/\n}\n\n.divTspanitle:hover {\n    color: #ccc;\n    text-decoration: none;\n    background: #333;\n}\n\n.divTspanitleSeparator {\n    position: relative;\n    visibility: visible;\n    text-overflow: ellipsis;\n    overflow: hidden;\n    margin: 0px;\n    white-space: nowrap;\n    display: inline-block;\n    max-width: 766px;\n    font-size: 12px;\n    padding: 10px;\n}\n\n\n/*_________________________  Progress  ________________________*/\n\n.progressbar {\n    position: relative;\n    visibility: visible;\n    width: 100%;\n    display: inline-block;\n    cursor: pointer;\n    height: 10px;\n    padding-top: 2px;\n    padding-right: 1px;\n    bottom: 10px;\n}\n\n.volumebar {\n    width: 100%;\n    height: 25px;\n}\n\n\n/*_________________________ RANGE ________________________*/\n\ninput[type=range] {\n    -webkit-appearance: none;\n    border: none;\n    display: inline-block;\n    width: 100%;\n    margin-left: auto;\n    margin-right: auto;\n    vertical-align: top;\n}\n\ninput[type=range]::-webkit-slider-r